import React, { useState } from "react";
import { ReactComponent as RightIcon } from "../../assets/img/icon-right.svg";

import {
  FooterWrapper,
  FooterTitleStyled,
  FooterSubtitleStyled,
  FooterFormStyled,
  InputStyled,
  SubscribeButton,
  FooterLinks,
  FooterColumn,
} from "./footer.styles";

const Footer = () => {
  const [email, setEmail] = useState("");
  const [invalidEmail, setInvalidEmail] = useState(false);

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
    setInvalidEmail(false);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    if (!email.includes("@") || !email.includes(".")) {
      setInvalidEmail(true);
    }
  };

  return (
    <FooterWrapper>
      <FooterTitleStyled>subscribe to news</FooterTitleStyled>
      <FooterSubtitleStyled>
        Receive or newsletter on the latest deals and happenings. <br />
        You can unsubscirbe any time.
      </FooterSubtitleStyled>
      <FooterFormStyled onSubmit={handleSubmit}>
        <InputStyled
          onChange={(event) => handleEmailChange(event)}
          type="text"
          placeholder="your email address"
          invalid={invalidEmail}
        />
        <SubscribeButton type="submit">
          <RightIcon></RightIcon>
        </SubscribeButton>
      </FooterFormStyled>
      <FooterLinks>
        <FooterColumn>
          <h3>help</h3>
          <ul>
            <li>
              <a href="/">Frequently Asked Questions</a>
            </li>
            <li>
              <a href="/">Help</a>
            </li>
            <li>
              <a href="/">How To Return</a>
            </li>
            <li>
              <a href="/">Contact Us</a>
            </li>
          </ul>
        </FooterColumn>
        <FooterColumn>
          <h3>delivery options</h3>
          <ul>
            <li>
              <a href="/">UK Next Day</a>
            </li>
            <li>
              <a href="/">UK Standard</a>
            </li>
            <li>
              <a href="/">EU Standard</a>
            </li>
            <li>
              <a href="/">EU Express</a>
            </li>
          </ul>
        </FooterColumn>
        <FooterColumn>
          <h3>about us</h3>
          <ul>
            <li>
              <a href="/">About Us</a>
            </li>
            <li>
              <a href="/">Affilities</a>
            </li>
            <li>
              <a href="/">Blog</a>
            </li>
            <li>
              <a href="/">Careers</a>
            </li>
            <li>
              <a href="/">Sitemap</a>
            </li>
          </ul>
        </FooterColumn>
      </FooterLinks>
    </FooterWrapper>
  );
};

export default Footer;
